// app/page.tsx

"use client";

import React, { useRef, isValidElement } from "react";
import Link from "next/link";
// [수정] 1. 'outline' 아이콘으로 변경
import {
  CheckCircleIcon,
  PencilSquareIcon,
  CpuChipIcon,
  ArrowDownTrayIcon,
} from "@heroicons/react/24/outline";
import { motion, Variants } from "framer-motion";

// 공통 Variants (수정 없음)
const fadeInUp: Variants = {
  initial: {
    opacity: 0,
    y: 30,
  },
  animate: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.6,
      ease: "easeInOut",
    },
  },
};

export default function HomePage() {
  const howItWorksRef = useRef<HTMLElement>(null);

  const handleScrollToWorks = () => {
    howItWorksRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <main className="w-full overflow-x-hidden">
      {/* Hero 섹션 */}
      <section className="bg-gray-900 text-white">
        <div className="container mx-auto flex max-w-5xl flex-col items-center px-6 py-24 text-center">
          <motion.h1
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.1 }}
            // [수정] 2. 타이포그래피: 줄 간격 조정
            className="text-4xl font-extrabold sm:text-5xl md:text-6xl leading-snug"
          >
            강사를 위한{" "}
            {/* [수정] 3. 색상: blue -> indigo */}
            <span className="text-indigo-400">프리미엄 컨텐츠</span>,
            <br />
            이제 간편하게 요청하세요.
          </motion.h1>
          <motion.p
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="mt-6 max-w-2xl text-lg text-gray-300"
          >
            RuleMakers는 고객님의 요청에 맞춘 고품질 모의고사와 N제
            컨텐츠를
            <br />
            신속하고 정확하게 제작합니다.
          </motion.p>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.3 }}
            className="mt-10 flex flex-col gap-4 sm:flex-row"
          >
            <button
              onClick={handleScrollToWorks}
              // [수정] 3. 색상: blue -> indigo
              className="rounded-md bg-indigo-600 px-8 py-3 text-base font-medium text-white shadow-lg transition-colors hover:bg-indigo-700"
            >
              지금 작업 요청하기
            </button>
            <Link
              href="/showcase"
              className="rounded-md bg-transparent px-8 py-3 text-base font-medium text-white ring-1 ring-white hover:bg-white hover:text-gray-900"
            >
              컨텐츠 샘플 보기
            </Link>
          </motion.div>
        </div>
      </section>

      {/* 특징(Features) 섹션 */}
      <motion.section
        variants={fadeInUp}
        initial="initial"
        whileInView="animate"
        viewport={{ once: true }}
        // [수정] 4. 여백: py-24 -> py-32
        className="bg-white py-32"
      >
        <div className="container mx-auto max-w-5xl px-6">
          <div className="text-center">
            <h2 className="text-3xl font-bold text-gray-900">
              RuleMakers만의 핵심 역량
            </h2>
            <p className="mt-4 text-lg text-gray-600">
              시간이 많이 소요되는 컨텐츠 제작, 이제 전문가에게 맡기세요.
            </p>
          </div>
          <div className="mt-16 grid gap-8 md:grid-cols-3">
            <FeatureCard
              icon={<CheckCircleIcon />}
              title="학교별 맞춤 제작"
              description="담당 학교의 최신 기출 경향과 부교재를 완벽하게 분석하여 맞춤형 컨텐츠를 제공합니다."
              delay={0.1}
            />
            <FeatureCard
              icon={<CheckCircleIcon />}
              title="신속한 제작"
              description="체계화된 제작 파이프라인을 통해 요청하신 마감일에 맞춰 신속하게 결과물을 전달합니다."
              delay={0.2}
            />
            <FeatureCard
              icon={<CheckCircleIcon />}
              title="전문가 검수"
              description="현직 강사 및 전문 컨텐츠 연구진의 다단계 검수를 거쳐 문항의 퀄리티를 보장합니다."
              delay={0.3}
            />
          </div>
        </div>
      </motion.section>

      {/* "작업 방식" 섹션 */}
      <motion.section
        ref={howItWorksRef}
        variants={fadeInUp}
        initial="initial"
        whileInView="animate"
        viewport={{ once: true }}
        // [수정] 4. 여백: py-24 -> py-32
        className="bg-gray-50 py-32"
      >
        <div className="container mx-auto max-w-5xl px-6">
          <div className="text-center">
            <h2 className="text-3xl font-bold text-gray-900">
              간편한 작업 요청 방식
            </h2>
            <p className="mt-4 text-lg text-gray-600">
              단 3단계로 프리미엄 컨텐츠를 완성합니다.
            </p>
          </div>

          <div className="mt-16 grid gap-8 md:grid-cols-3">
            <ProcessStep
              icon={<PencilSquareIcon />}
              step="Step 1"
              title="작업 요청 및 자료 전송"
              description="전용 요청 폼을 통해 상세 내용과 참고 파일(기출 PDF 등)을 업로드합니다."
              delay={0.1}
            />
            <ProcessStep
              icon={<CpuChipIcon />}
              step="Step 2"
              title="컨텐츠 제작 및 검수"
              description="전문 제작팀이 요청에 맞춰 컨텐츠를 제작하고, 다단계 검수팀이 퀄리티를 보장합니다."
              delay={0.2}
            />
            <ProcessStep
              icon={<ArrowDownTrayIcon />}
              step="Step 3"
              title="결과물 다운로드"
              description="강사님 전용 대시보드에서 완성된 컨텐츠(PDF)를 즉시 다운로드하실 수 있습니다."
              delay={0.3}
            />
          </div>

          <motion.div
            variants={fadeInUp}
            initial="initial"
            whileInView="animate"
            viewport={{ once: true }}
            className="mt-16 text-center"
          >
            <Link
              href="/request"
              // [수정] 3. 색상: blue -> indigo
              className="rounded-md bg-indigo-600 px-10 py-4 text-lg font-medium text-white shadow-lg transition-colors hover:bg-indigo-700"
            >
              지금 바로 시작하기
            </Link>
          </motion.div>
        </div>
      </motion.section>

      {/* 최종 CTA 섹션 */}
      <motion.section
        variants={fadeInUp}
        initial="initial"
        whileInView="animate"
        viewport={{ once: true }}
        className="bg-gray-900 text-white"
      >
        {/* [수정] 4. 여백: py-20 -> py-28 */}
        <div className="container mx-auto flex max-w-4xl flex-col items-center justify-between gap-8 px-6 py-28 text-center md:flex-row md:text-left">
          <div>
            <h2 className="text-3xl font-bold">
              준비되셨나요?
            </h2>
            <p className="mt-3 text-lg text-gray-300">
              고객님의 첫 번째 프리미엄 컨텐츠를 지금 바로 요청해보세요.
            </p>
          </div>
          <Link
            href="/request"
            className="flex-shrink-0 rounded-md bg-white px-8 py-3 text-base font-medium text-gray-900 shadow-lg transition-colors hover:bg-gray-200"
          >
            작업 요청 페이지로 이동
          </Link>
        </div>
      </motion.section>
    </main>
  );
}

// Card Variants (수정 없음)
const cardVariants: Variants = {
  offscreen: {
    opacity: 0,
    y: 30,
  },
  onscreen: (delay: number) => ({
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.5,
      ease: "easeInOut",
      delay: delay,
    },
  }),
};

// FeatureCard 컴포넌트
function FeatureCard({
  icon,
  title,
  description,
  delay,
}: {
  icon: React.ReactNode;
  title: string;
  description: string;
  delay: number;
}) {
  return (
    <motion.div
      variants={cardVariants}
      initial="offscreen"
      whileInView="onscreen"
      custom={delay}
      viewport={{ once: true }}
      // [수정] 4. 그림자: hover:shadow-xl
      whileHover={{
        y: -5,
        boxShadow:
          "0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1)", // shadow-xl
      }}
      transition={{ duration: 0.2, ease: "easeOut" }}
      // [수정] 4. 그림자: shadow-sm -> shadow-lg
      className="rounded-lg bg-gray-50 p-8 shadow-lg"
    >
      {/* [수정] 3. 색상: blue -> indigo / bg-blue-100 -> bg-indigo-50 */}
      <div className="inline-flex h-12 w-12 items-center justify-center rounded-full bg-indigo-50 text-indigo-600">
        {icon}
      </div>
      <h3 className="mt-6 text-xl font-bold text-gray-900">{title}</h3>
      <p className="mt-4 text-base text-gray-600">{description}</p>
    </motion.div>
  );
}

// ProcessStep 컴포넌트
function ProcessStep({
  icon,
  step,
  title,
  description,
  delay,
}: {
  icon: React.ReactNode;
  step: string;
  title: string;
  description: string;
  delay: number;
}) {
  return (
    <motion.div
      variants={cardVariants}
      initial="offscreen"
      whileInView="onscreen"
      custom={delay}
      viewport={{ once: true }}
      // [수정] 4. 그림자: shadow -> shadow-lg
      className="rounded-lg bg-white p-8 text-center shadow-lg"
    >
      {/* [수정] 3. 색상: blue -> indigo / bg-blue-100 -> bg-indigo-50 */}
      <div className="mx-auto inline-flex h-16 w-16 items-center justify-center rounded-full bg-indigo-50 text-indigo-600">
        {icon &&
          isValidElement(icon) &&
          React.cloneElement(
            icon as React.ReactElement<{ className?: string }>,
            { className: "h-8 w-8" }
          )}
      </div>
      {/* [수정] 3. 색상: blue -> indigo */}
      <p className="mt-6 text-sm font-semibold uppercase text-indigo-600">{step}</p>
      <h3 className="mt-2 text-xl font-bold text-gray-900">{title}</h3>
      <p className="mt-3 text-base text-gray-600">{description}</p>
    </motion.div>
  );
}